<template>
  <div id="BSTree">
    <h1>二叉查找树</h1>
    <div class="show-view">
      <h2>数据展示</h2>
      <div>{{bstree}}</div>
      <canvas width="600" height="400" id="canvas-tree"></canvas>
    </div>
    <div class="show-data">
      <h2>结果：</h2>
      {{result}}
    </div>
    <div class="operate-data">
      <h2>数据操作</h2>
      <div class="operate-data-item">
        <button @click.stop.prevent="insertBSTree">插入</button>
        <input type="text" v-model="insertData" name="insertData" placeholder="数据">
        <span class="oprate-data-aux"><=数据（数字）</span>
      </div>
      <div class="operate-data-item">
        <button @click.stop.prevent="deleteBSTree">删除</button>
        <input type="text" v-model="deleteKey" name="deleteKey" placeholder="关键字">
        <span class="oprate-data-aux"><=关键字</span>
      </div>
      <div class="operate-data-item">
        <button @click.stop.prevent="searchBSTree">查找</button>
        <input type="text" v-model="searchKey" name="searchKey" placeholder="关键字">
        <span class="oprate-data-aux"><=关键字</span>
      </div>
    </div>
    <div class="summary">
      <h2>总结：</h2>
      <h3>二叉查找树结构：</h3>
      <pre>
        {
          data // 数据
          LTree // 左子树
          RTree // 右子树
        }
      </pre>
      <h3>接口：</h3>
      <pre>
        search(key) // 按关键字查找，查无返回false
        insert(data) // 插入数据，成功返回true，失败返回false
        delete(key) // 按关键字删除，成功返回删除的数据，失败返回false
      </pre>
    </div>
  </div>
</template>

<script>
function BSTree (data = null, LTree = null, RTree = null) {
  this.data = data
  this.LTree = LTree
  this.RTree = RTree
}
BSTree.prototype.search = function (key) {
  if (this.data === null) {
    return false
  } else {
    if (key === this.data.key) {
      return {
        key: this.data.key,
        value: this.data.value
      }
    } else if (key > this.data.key && this.RTree !== null) {
      return this.RTree.search(key)
    } else if (key < this.data.key && this.LTree !== null) {
      return this.LTree.search(key)
    } else {
      return false
    }
  }
}
BSTree.prototype.insert = function (data) {
  if (this.data === null) {
    this.data = data
    return true
  } else {
    if (data.key === this.data.key) {
      return false
    } else if (data.key > this.data.key) {
      if (this.RTree === null) {
        this.RTree = new BSTree(data)
        return true
      } else {
        return this.RTree.insert(data)
      }
    } else if (data.key < this.data.key) {
      if (this.LTree === null) {
        this.LTree = new BSTree(data)
        return true
      } else {
        return this.LTree.insert(data)
      }
    }
  }
}
BSTree.prototype.delete = function (key) {
  if (this.data === null) {
    return false
  } else if (this.LTree === null && this.RTree === null && this.data.key === key) {
    let returnData = {
      key: this.data.key,
      value: this.data.value
    }
    this.data = null
    return returnData
  } else {
    if (key === this.data.key) {
      let returnData = {
        key: this.data.key,
        value: this.data.value
      }
      if (this.LTree === null) {
        this.data = this.RTree.data
        this.LTree = this.RTree.LTree
        this.RTree = this.RTree.RTree
      } else if (this.RTree === null) {
        this.data = this.LTree.data
        this.RTree = this.LTree.RTree
        this.LTree = this.LTree.LTree
      } else {
        let itemParent = this
        let item = this.LTree
        while (item.RTree !== null) {
          itemParent = item
          item = item.RTree
        }
        this.data = item.data
        if (itemParent === this) {
          this.LTree = item.LTree
        } else {
          itemParent.RTree = item.LTree
        }
        return returnData
      }
    } else if (key > this.data.key && this.RTree !== null) {
      if (this.RTree.data.key === key && this.RTree.RTree === null && this.RTree.LTree === null) {
        let returnData = {
          key: this.RTree.data.key,
          value: this.RTree.data.value
        }
        this.RTree = null
        return returnData
      }
      return this.RTree.delete(key)
    } else if (key < this.data.key && this.LTree !== null) {
      if (this.LTree.data.key === key && this.LTree.RTree === null && this.LTree.LTree === null) {
        let returnData = {
          key: this.LTree.data.key,
          value: this.LTree.data.value
        }
        this.RTree = null
        return returnData
      }
      return this.LTree.delete(key)
    } else {
      return false
    }
  }
}
export default {
  data () {
    return {
      bstree: null,
      insertData: '',
      deleteKey: '',
      searchKey: '',
      result: '无'
    }
  },
  mounted () {
    this.bstree = new BSTree()
  },
  methods: {
    paintTree () {
      let canvas = document.getElementById('canvas-tree')
      let cxt = canvas.getContext('2d')
      canvas.width = canvas.width
      canvas.height = canvas.height
      function paintNode (tree, level, index) {
        let parentIndex = Math.ceil(index / 2)
        cxt.fillStyle = 'black'
        cxt.strokeStyle = 'red'
        cxt.beginPath()
        cxt.arc(600 / (Math.pow(2, level - 1) + 1) * index, (level - 1) * 100 + 50, 30, 0, Math.PI * 2, true)
        cxt.closePath()
        cxt.stroke()
        cxt.font = '30px serif'
        cxt.textAlign = 'center'
        if (tree.data.value !== null) {
          cxt.fillText(tree.data.value, 600 / (Math.pow(2, level - 1) + 1) * index, (level - 1) * 100 + 50)
        }
        if (level !== 1) {
          cxt.moveTo(600 / (Math.pow(2, level - 2) + 1) * parentIndex, (level - 2) * 100 + 50)
          cxt.lineTo(600 / (Math.pow(2, level - 1) + 1) * index, (level - 1) * 100 + 50)
          cxt.stroke()
        }
        if (tree.LTree) paintNode(tree.LTree, level + 1, index * 2 - 1)
        if (tree.RTree) paintNode(tree.RTree, level + 1, index * 2)
      }
      paintNode(this.bstree, 1, 1)
    },
    insertBSTree () {
      this.result = this.bstree.insert({
        key: parseInt(this.insertData),
        value: this.insertData
      })
      this.paintTree()
    },
    deleteBSTree () {
      this.result = this.bstree.delete(parseInt(this.deleteKey))
      this.paintTree()
    },
    searchBSTree () {
      this.result = this.bstree.search(parseInt(this.searchKey))
      this.paintTree()
    }
  }
}
</script>

<style scoped>
#BSTree {
  font-size: 30px;
}
.show-view {
  background-color: #adf;
}
.view-item {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 5px;
  line-height: 50px;
  background-color: #fad;
}
.show-data {
  background-color: #fda;
}
.operate-data {
  background-color: #dfa;
}
.operate-data button {
  font-size: 20px;
  border: none;
  background-color: white;
  width: 70px;
  height: 40px;
  margin: 10px;
  cursor: pointer;
}
.operate-data input {
  width: 100px;
  height: 37px;
  padding: 0;
  position: relative;
  right: 17px;
  bottom: 4px;
  text-align: center;
}
.oprate-data-aux {
  font-size: 20px;
  position: relative;
  right: 17px;
}
.summary {
  background-color: #afe;
}
</style>



